.banner-card{
  width:100vw;
  margin-top: 1vh;
  margin-bottom: 3vh;
  /* position: relative; */
  height: 70vh;
  color: white;
  
}

.banner-img{
  height: 70vh;
  width: 100vw;
}

.banner-title{
  position: absolute;
  top: 45vh;
  left: 5vw;
}
.banner-text{
  position: absolute;
  top: 55vh;
  left: 5vw;
  height: 5.5rem;
  overflow:hidden;
  
  margin-right: 5vw;
}

.moviescontainer{
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.movies-list{
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;
  justify-content: center;
}

.movies-card{
width: 20vw;
margin:1rem;
color:white;
position: relative;
}

.movies-title{
  position: absolute;
  top: 1rem;
  left: 1rem;
}

.movies-button{
  position: absolute;
  bottom:2rem;
  

}
.movies-image{
  height:40vh;
  width:20vw;
}

.button-wrapper{
  display: flex;
  width: 100%;
  justify-content: center;
}

.favourites-genres{
  padding:3rem;
}

.favourites-table{
padding:3rem;
overflow:auto;
}






@media screen and (max-width:400px) {
  .banner-card{
    height:30vh;
   
  }
  .banner-img{
    height:30vh;
  }
  .banner-title{
    top:12vh;
    left:5vw;
  }
  .banner-text{
    top:17vh;
    left:5vw;
  }
  .movies-card{
    width:40vw;
    
    margin:0.1rem;
  }
  .movies-button{
    width: 60%;
  }
  .movies-image{
    width:40vw;
  }
 
}


@media screen and (max-width:600px ) {
  .banner-card{
    height:30vh;
   
  }
